@media only screen and (max-width: 37.5em) {
  .section-portfolio__title--text {
    padding: 0 2rem;
  }
}

.project-details {
  position: relative;
  height: 100vh;
  margin-bottom: 10rem;
}

.project-info {
  width: 35rem;
  height: auto;
  position: absolute;
  top: 55%;
  left: 8%;
  z-index: 100;
}

.project-info__title {
  //   color: #ffd64d;
  font-size: 3rem;
}

.project-info__description {
  background-color: #fff;
  padding: 1.5rem 1.5rem;
  border-radius: 0.3rem;
  margin: 1rem 0;
  line-height: 2rem;
  box-shadow: 1.5rem 2rem 6rem rgba(0, 0, 0, 0.2);
}

.project-info__stack {
  display: flex;
  justify-content: space-between;
}

.project-info__stack--list {
  list-style: none;
  font-weight: 600;
  font-style: italic;
}

.project-info__links {
  display: flex;
  justify-content: start;
  align-items: center;
  margin-top: 1.5rem;
}

.project-info__links--anchor {
  margin-right: 2rem;
}

.project-info__links--anchor-img {
  height: 2rem;
  width: auto;
}

.project-image {
  position: absolute;
  top: 10%;
  left: 25%;
  z-index: -100;
}

.project-image__img {
  width: 70vw;
  height: auto;
}

.project-image__img--1 {
  height: 50rem;
  width: auto;
}
